import React from 'react'
import { withStyles,makeStyles } from '@material-ui/styles';
import {
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
  } from "@material-ui/core";

const StyledTableCell = withStyles(theme => ({
    head:{
        backgroundColor: '#246DFF', 
        color: 'white',
    },
    body:{
        fontSize: 14,
    },

}))(TableCell);

const StyledTableRow = withStyles(theme=>({
    row: {
        '&:hover':{backgroundColor: 'rgba(33,150,243,0.5'}
    }
}))(TableRow);

// function createData(id, col1, col2, col3, col4, col5){
//     return{id, col1, col2, col3, col4, col5};
// }
function createData(id,city,district,medptName,address,phMedpt,opHrWeek,opHrSat,
    opHrSunEtc,bogunso,phBogunso,notes){
    return{id,city,district,medptName,address,phMedpt,opHrWeek,opHrSat,
        opHrSunEtc,bogunso,phBogunso,notes};
}

const rows = [
    createData(1, '','','','2 8','0000','0000','0000','0000','0000','0000','sunday X'),
    createData(254, '', '', ' ', '   95 ()', '0830~1730', '', '',  '031-8046-5000', '', '031-678-5724', '--'),
    createData(1, '','','','2 8','0000','0000','0000','0000','0000','0000','sunday X'),
    createData(254, '', '', ' ', '   95 ()', '0830~1730', '', '',  '031-8046-5000', '', '031-678-5724', '--'),
    createData(1, '','','','2 8','0000','0000','0000','0000','0000','0000','sunday X'),
    createData(254, '', '', ' ', '   95 ()', '0830~1730', '', '',  '031-8046-5000', '', '031-678-5724', '--'),
    createData(1, '','','','2 8','0000','0000','0000','0000','0000','0000','sunday X'),
    createData(254, '', '', ' ', '   95 ()', '0830~1730', '', '',  '031-8046-5000', '', '031-678-5724', '--'),
    createData(1, '','','','2 8','0000','0000','0000','0000','0000','0000','sunday X'),
    createData(254, '', '', ' ', '   95 ()', '0830~1730', '', '',  '031-8046-5000', '', '031-678-5724', '--'),
    createData(1, '','','','2 8','0000','0000','0000','0000','0000','0000','sunday X'),
    createData(254, '', '', ' ', '   95 ()', '0830~1730', '', '',  '031-8046-5000', '', '031-678-5724', '--'),
];
//   { id: 1, city: '', district: '', medptName: '', address: '2 8', opHrWeek: '0000', opHrSat: '0000', opHrSunEtc: '0000', phMedpt: '0000', bogunso: '0000', phBogunso: '0000', notes:'not open on sundays'},
//   { id: 254, city: '', district: '', medptName: ' ', address: '   95 ()', opHrWeek: '0830~1730', opHrSat: '', opHrSunEtc: '', phMedpt: '031-8046-5000', bogunso: '', phBogunso: '031-678-5724', notes:'--'},

const useStyles = makeStyles({
    table:{
        minWidth:700,
    },
});


export default function MedptTable(){
    const classes = useStyles();
    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="customized table">
                <TableHead>
                    <TableRow>
                        <StyledTableCell onclick="sortColumn('id')">/ medpt_id</StyledTableCell>
                        <StyledTableCell onclick="sortColumn('city')">/ medpt_city</StyledTableCell>
                        <StyledTableCell>/ medpt_district</StyledTableCell>
                        <StyledTableCell>/medpt_name</StyledTableCell>
                        <StyledTableCell> / <>medpt_address</></StyledTableCell>
                        <StyledTableCell> / <>medpt_phone</></StyledTableCell>
                        <StyledTableCell> / <>medpt_hr_wkday</></StyledTableCell>
                        <StyledTableCell> <>medpt_hr_sat</></StyledTableCell>
                        <StyledTableCell>` <>medpt_hr_holiday</></StyledTableCell>
                        <StyledTableCell><>medpt_gov</></StyledTableCell>
                        <StyledTableCell> <>medpt_gov_phone</></StyledTableCell>
                        <StyledTableCell>note/ <>medpt_note</></StyledTableCell>
                        {/* onclick sto sortCol by values */}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row)=>(
                        <StyledTableRow key={row.id}>
                            <StyledTableCell component ="th" scope="row">
                                {row.id}
                            </StyledTableCell>
                            <StyledTableCell align="center">{row.city}</StyledTableCell>
                            <StyledTableCell align="center">{row.district}</StyledTableCell>
                            <StyledTableCell align="center">{row.medptName}</StyledTableCell>
                            <StyledTableCell align="center">{row.address}</StyledTableCell>
                            <StyledTableCell align="center">{row.phMedpt}</StyledTableCell>
                            <StyledTableCell align="center">{row.opHrWeek}</StyledTableCell>
                            <StyledTableCell align="center">{row.opHrSat}</StyledTableCell>
                            <StyledTableCell align="center">{row.opHrSunEtc}</StyledTableCell>
                            <StyledTableCell align="center">{row.bogunso}</StyledTableCell>
                            <StyledTableCell align="center">{row.phBogunso}</StyledTableCell>
                            <StyledTableCell align="center">{row.notes}</StyledTableCell>
                        </StyledTableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}