import React from 'react';
import _ from 'lodash';

const Pagination = (props) => {
  const { itemsCount, pageSize, currentPage, onPageChange } = props; //  () ,    () 

  const pageCount = Math.ceil(itemsCount / pageSize); //    
  if (pageCount === 1) return null; // 1     

  const pages = _.range(1, pageCount + 1); //      +1, https://lodash.com/docs/#range 

  return (
    <nav> {/* VSCode : nav>ul.pagination>li.page-item>a.page-link */}
      <ul className="pagination">
        {pages.map(page => (
          <li 
            key={page} 
            className={page === currentPage ? "page-item active" : "page-item"} // Bootstrap     
            style={{ cursor: "pointer" }}>
              <a className="page-link" onClick={() => onPageChange(page)}>{page}</a> {/*       */}
          </li>
        ))}
      </ul>
    </nav>
  );
}

export default Pagination;