import React, { useEffect, useState, useRef } from 'react';
import Pagination from '../common/Pagination';
import { paginate } from '../utils/paginate';
import axios from 'axios';

import { Link } from "react-router-dom";
 
const QnAList = () => {
    const getQnA = () => { //    
      const QnA = [
        { no: "1", title: " 1", writer: "", date: "2019-07-17", review: "5"},
        { no: "2", title: " 2", writer: "", date: "2019-07-17", review: "5"},
        { no: "3", title: " 1", writer: "", date: "2019-07-17", review: "5"},
        { no: "4", title: " 3", writer: "", date: "2019-07-17", review: "5"},
        { no: "5", title: " 1", writer: "", date: "2019-07-17", review: "5"},
        { no: "6", title: " 4", writer: "", date: "2019-07-17", review: "5"},
        { no: "7", title: " 2", writer: "", date: "2019-07-17", review: "5"},
        { no: "8", title: " 2", writer: "", date: "2019-07-17", review: "5"},
        { no: "9", title: " 5", writer: "", date: "2019-07-17", review: "5"},
        { no: "10", title: " 6", writer: "", date: "2019-07-17", review: "5"},
        { no: "11", title: " 1", writer: "", date: "2019-07-17", review: "5"},
        { no: "12", title: " 4", writer: "", date: "2019-07-17", review: "5"},
        { no: "13", title: " 2", writer: "", date: "2019-07-17", review: "5"},
        { no: "14", title: " 2", writer: "", date: "2019-07-17", review: "5"},
        { no: "15", title: " 5", writer: "", date: "2019-07-17", review: "5"}
      ]
      
      return QnA;
    }
  
    const [QnA, setQnA] = useState({
        data: getQnA(),
        pageSize: 10,
        currentPage: 1
    });

    const handlePageChange = (page) => {
        setQnA({ ...QnA, currentPage: page });
      }
    
    const { data, pageSize, currentPage } = QnA;

    const pagedQnA = paginate(data, currentPage, pageSize);
  
    const { length: count } = QnA.data;
    
    if(count === 0)
      return <p> .</p>

    return (
        <>
        <p>{count}   .</p>
        <div class="ui middle aligned center aligned grid">
            <br/><h2 class="ui teal image header">
                QnA 
            </h2>
            <table className="table" style={{width: "100%"}}>

                <colgroup>
                    <col width="10%" />
                    <col width="40%" />
                    <col width="10%" />
                    <col width="20%" />
                    <col width="10%" />
                </colgroup>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {pagedQnA.map((QnA) => (
                        <tr key={QnA.title}>
                            <td>{QnA.no}</td>
                            <td>{QnA.title}</td>
                            <td>{QnA.writer}</td>
                            <td>{QnA.date}</td>
                            <td>{QnA.review}</td>
                        </tr>
                    ))}
                </tbody>

                
            
                <Pagination
                    pageSize={pageSize} 
                    currentPage={currentPage} 
                    itemsCount={count}
                    onPageChange={handlePageChange}
                />
                
                
                {/* <nav aria-label="Page navigation example">
                    <ul className="pagination">
                    <li className="page-item"><a className="page-link" href="#">Previous</a></li>
                    <li className="page-item"><a className="page-link" href="#">1</a></li>
                    <li className="page-item"><a className="page-link" href="#">2</a></li>
                    <li className="page-item"><a className="page-link" href="#">3</a></li>
                    <li className="page-item"><a className="page-link" href="#">Next</a></li>
                    </ul>
                </nav> */}
                
            </table><br/>
            <table>
                <div class="search-wrap">
                    <div class="col-4 input-group">
                        <input id="Search" type="text" class="form-control kw" />
                        <div class="input-group-append">
                            <button class="btn btn-outline-secondary" type="button" id="btn_search"></button>
                        </div>                    
                        <Link to="/write" style={{marginLeft:"900px"}}>                        
                            <button class="btn btn-outline-secondary"> </button>                        
                        </Link>
                    </div>    
                </div>
            </table>       
        </div>
    </>);
};
 
export default QnAList;





