import ChatBot from "react-simple-chatbot";
import React from "react";
import { ThemeProvider } from "styled-components";
import { useDispatch } from "react-redux";
import { initStatus } from "features/chatbot/reducer/chatbotSlice";
import { AlertMessage, Ask, CheckStatus, ResetMessage } from "./ChatModules";

export default function Chat() {
  const dispatch = useDispatch()
  dispatch(initStatus())
  return (
    <ThemeProvider
      theme={{
        background: "#f5f8fb",
        fontFamily: "Helvetica Neue",
        headerBgColor: "#EF6C00",
        headerFontColor: "#fff",
        headerFontSize: "15px",
        botBubbleColor: "#EF6C00",
        botFontColor: "#fff",
        userBubbleColor: "#fff",
        userFontColor: "#4a4a4a",
      }}
    >
      <ChatBot
        steps={[
          {
            id: 'welcome',
            message: '~     ?',
            trigger: 'select',
          },
          { 
            id: "select", 
            options: [ 
              { value: "y", label: "   ", trigger: "fever" }, 
              { value: "n", label: "", trigger: "question" }, 
            ], 
          }, 
          {
            id: 'question',
            message: '  ?',
            trigger: 'userinput'
          },
          {
            id: 're_quest',
            component: <ResetMessage />,
            asMessage: true,
            trigger: 'userinput'
          },
          {
            id: 'userinput',
            user: true,
            trigger: 'askBot'
          },
          
          {
            id: 'dynamicallyReachedStep',
            message: 'This step was reached dynamically.',
            trigger: 'userinput'
          },
          {
            id: "askBot",
            component: <Ask />,
            asMessage: true,
            trigger: 'userinput'
          },
          {
            id: "checkBot",
            component: <CheckStatus />,
            asMessage: true,
            trigger: 'userinput'
          },
          {
            id: 'fever',
            message: '  ?',
            trigger: 'select_fever'
          },
          {
            id: 'select_fever',
            options: [ 
              { value: "fever1", label: "38.4 ", trigger: "checkBot" }, 
              { value: "fever2", label: "38.5-38.9", trigger: "checkBot" }, 
              { value: "fever3", label: "39.0-40", trigger: "checkBot" }, 
              { value: "fever4", label: "40.1 ", trigger: "checkBot" }, 
              { value: "0", label: " ", trigger: "ino_area" }, 
            ], 
          },
          {
            id: 'ino_area',
            message: '   ?',
            trigger: 'select_ino_area'
          },
          {
            id: 'select_ino_area',
            options: [ 
              { value: "ino_area1", label: "     ", trigger: "checkBot" }, 
              { value: "ino_area2", label: "1~2      ", trigger: "checkBot" }, 
              { value: "ino_area3", label: "3           ", trigger: "checkBot" }, 
              { value: "0", label: " ", trigger: "swell" }, 
            ], 
          },
          {
            id: 'swell',
            message: '   ?',
            trigger: 'select_swell'
          },
          {
            id: 'select_swell',
            options: [ 
              { value: "swell1", label: " 5cm ", trigger: "checkBot" }, 
              { value: "swell2", label: "5.1~10cm  ", trigger: "checkBot" }, 
              { value: "swell3", label: "10cm   ", trigger: "checkBot" }, 
              { value: "swell4", label: "", trigger: "checkBot" }, 
              { value: "0", label: " ", trigger: "sick" }, 
            ], 
          },
          {
            id: 'sick',
            message: '    ?',
            trigger: 'select_sick'
          },
          {
            id: 'select_sick',
            options: [ 
              { value: "sick1", label: "   ", trigger: "checkBot" }, 
              { value: "sick2", label: "(24) 1~2 ", trigger: "checkBot" }, 
              { value: "sick3", label: "(24) 3  ", trigger: "checkBot" }, 
              { value: "0", label: " ", trigger: "pain" }, 
            ], 
          },
          {
            id: 'pain',
            message: ' ?',
            trigger: 'select_pain'
          },
          {
            id: 'select_pain',
            options: [ 
              { value: "pain3", label: "", trigger: "checkBot" }, 
              { value: "pain4", label: "", trigger: "checkBot" }, 
              { value: "pain5", label: "", trigger: "checkBot" }, 
              { value: "0", label: " ", trigger: "fatigue" }, 
            ], 
          },
          {
            id: 'fatigue',
            message: '  ?',
            trigger: 'select_fatigue'
          },
          {
            id: 'select_fatigue',
            options: [ 
              { value: "fatigue1", label: "  ", trigger: "checkBot" }, 
              { value: "0", label: " ", trigger: "allergy" }, 
            ], 
          },
          {
            id: 'allergy',
            message: '  ?',
            trigger: 'select_allergy'
          },
          {
            id: 'select_allergy',
            options: [ 
              { value: "allergy3", label: "", trigger: "checkBot" }, 
              { value: "allergy4", label: "", trigger: "checkBot" }, 
              { value: "allergy5", label: "()", trigger: "checkBot" }, 
              { value: "allergy6", label: "(/)", trigger: "checkBot" }, 
              { value: "0", label: " ", trigger: "etc" }, 
            ], 
          },
          {
            id: 'etc',
            message: '   ?',
            trigger: 'select_etc'
          },
          {
            id: 'select_etc',
            options: [ 
              { value: "etc3", label: "  ", trigger: "checkBot" }, 
              { value: "0", label: " ", trigger: "alert" }, 
            ], 
          },
          {
            id: 'alert',
            component: <AlertMessage />,
            asMessage: true,
            trigger: 're_quest'
          }
        ]}
        botAvatar={require("./data/icon.png").default}
        userAvatar={require("./data/user.png").default}
      />
    </ThemeProvider>
  );
};