import React from "react";
import {Navbar, Nav, NavDropdown} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from "react-router-dom";
import 'features/common/style/NaviStyle.css'



function Navi() {

return (
    <div className="Navigation">
        <Navbar bg="primary" variant="dark" id="centerboxs">
            <Nav className="mr-auto">s
            &emsp;<Link className="NavLink" to="/">Home</Link>
            &emsp;<NavDropdown title="" id="collasible-nav-dropdown" >
                    <Link className="NavLink2" to="/login"></Link>
                    <Link className="NavLink2" to="/user"></Link>
                </NavDropdown>
            &emsp;<Link className="NavLink" to= "/QnA">QnA</Link>
            &emsp;<NavDropdown title="" id="collasible-nav-dropdown">
                    <Link className="NavLink2" to= "/organ/local"></Link>
                    <Link className="NavLink2" to= "/organ/world"></Link>
                </NavDropdown>
            
                
            &emsp;<Link className="NavLink" to="/main/corona"></Link>
                
            &emsp;<NavDropdown title="" id="collasible-nav-dropdown">
                    <Link className="NavLink2"to= "/medpts"></Link>
                    <Link className="NavLink2"to= "/message"></Link>
                    <Link className="NavLink2"to= "/news"></Link>
                </NavDropdown>
            &emsp;<Link className="NavLink" to= "/organ/chat"></Link>
            
            </Nav>
        </Navbar>
    </div>
    )
}

export default Navi;


